const firebase = window.firebase;


import configureStore from './store/configureStore';
import Routes from './_routes';

const store = configureStore();

const rootElem = document.getElementById('app');

firebase.auth().onAuthStateChanged((user) => {
  console.log('user', user);
  if (user) {
    firebase.database.enableLogging(true);
    // Required for packaged app (alternative requires iframes and there's nasty CSP issues)
    firebase.database.INTERNAL.forceWebSockets();
    //console.log('Firebase database:', userDB);
    firebase.database().ref('users/' + user.uid + '/conversations/182')
      .on('value', (snapshot) => {
        console.log(snapshot.val());
      });
    setInterval(() => {
      //firebase.database().ref('users/' + user.uid + '/test').set(123);
    }, 10000);
  } else {
    console.log('Not signed in');
  }
});

let email = 'tyler34862@gmail.com';
let password = 'nicktylah1';

firebase.auth()
  .signInWithEmailAndPassword(email, password)
  .catch((err) => {
    console.debug(err);
    // Handle Errors here.
    var errorCode = err.code;
    var errorMessage = err.message;
    if (errorCode === 'auth/user-not-found') {
      firebase.auth().createUserWithEmailAndPassword(email, password)
        .catch((err) => {
          console.error(err);
        });
    } else {
      console.error(err);
    }
  });

render(
  <Provider store={store}>
    <App />
  </Provider>,
  rootElem
);
